/** @jsx jsx */
import { jsx } from '@emotion/core';
import {
  Button,
  makeStyles,
  Dialog,
  DialogTitle,
  DialogContent,
  DialogContentText,
  TextField,
  DialogActions,
} from '@material-ui/core';
import Emoji from 'components/Utils/Emoji';
import { Fragment, useState, useCallback, Dispatch, ChangeEvent, SetStateAction } from 'react';
import { useAuth } from 'hooks/auth/useAuth';
import red from '@material-ui/core/colors/red';

const SetUserInfoButton = () => {
  const { updateProfile } = useAuth();
  const style = makeStyles({
    needAddingInfoButton: {
      color: red['500'],
      marginTop: '0.5rem',
    },
  })();

  const [open, setOpen] = useState(false);
  const [name, setName] = useState('');
  const [isLoading, setLoading] = useState(false);

  const handleInputChange = useCallback((handler: Dispatch<SetStateAction<string>>) => {
    return (e: ChangeEvent<HTMLInputElement>) => handler(e.target.value);
  }, []);

  const handleClose = useCallback(() => {
    setOpen(false);
  }, []);

  const handleSubmit = useCallback(async () => {
    if (name.length === 0) {
      return;
    }

    setLoading(true);
    try {
      await updateProfile({ name });
      setOpen(false);
    } finally {
      setLoading(false);
    }
  }, [name, updateProfile]);

  return (
    <Fragment>
      <Button
        fullWidth
        variant="text"
        className={style.needAddingInfoButton}
        size="small"
        onClick={() => setOpen(true)}
      >
        <Emoji emoji="" name="warning" />   !
      </Button>
      <Dialog open={open} onClose={handleClose}>
        <DialogTitle>  </DialogTitle>
        <DialogContent>
          <DialogContentText>
              <Emoji emoji="" name="thanks" />
          </DialogContentText>
          <TextField autoFocus margin="dense" label="" fullWidth onChange={handleInputChange(setName)} />
        </DialogContent>
        <DialogActions>
          <Button color="primary" onClick={handleClose}>
             
          </Button>
          <Button color="primary" onClick={handleSubmit} disabled={isLoading}>
            
          </Button>
        </DialogActions>
      </Dialog>
    </Fragment>
  );
};

export default SetUserInfoButton;
